function DashboardAddLead(props:any) {

  return (
    <DashboardLeads
      breadcrumbs={[
        {id:1, text: 'Dashboard', href:'/'},
        {id:2, text: 'Leads', href:'/dashboard/leads'},
        {id:3, text: 'Add'}
      ]}
    >
      <AddLead />
    </DashboardLeads>
  );
}

export default DashboardAddLead;
