import * as React from 'react';

import Grid from '@mui/material/Grid';

import Breadcrumbs from '@mui/material/Breadcrumbs';
import Typography from '@mui/material/Typography';
import Stack from '@mui/material/Stack';
import NavigateNextIcon from '@mui/icons-material/NavigateNext';
import Link from '@mui/material/Link';


function handleClick(event: React.MouseEvent<HTMLAnchorElement, MouseEvent>) {
  event.preventDefault();
  console.info('You clicked a breadcrumb.');
}

function BreadcrumbLink(props:any) {
    return (
        <Link
        underline="hover"
        key={props.id}
        color="inherit"
        href={props.href}
        onClick={handleClick}
      >
        {props.text}
      </Link>
    );
}

function BreadcrumbText(props:any) {
    return (
        <Typography key={props.id} color="text.primary">
            {props.text}
        </Typography> 
    );
}

function CustomSeparator(props:any) {
  //console.log(props.breadcrumbs.slice(0, -1));
  //console.log(props.breadcrumbs[props.breadcrumbs.length-1]);
  const breadcrumbs = [ 
    ...props.breadcrumbs.slice(0, -1).map((a:any)=>{
      return <BreadcrumbLink href={a.href} id={a.id} text={a.text} key={a.id}/>
    }), 
    <BreadcrumbText 
      id={props.breadcrumbs[props.breadcrumbs.length-1].id} 
      text={props.breadcrumbs[props.breadcrumbs.length-1].text} 
      key={props.breadcrumbs[props.breadcrumbs.length-1].id}
    />];
  
  /*[
    <Link underline="hover" key="1" color="inherit" href="/" onClick={handleClick}>
      MUI
    </Link>,
    <Link
      underline="hover"
      key="2"
      color="inherit"
      href="/getting-started/installation/"
      onClick={handleClick}
    >
      Core
    </Link>,
    <Typography key="3" color="text.primary">
      Breadcrumb
    </Typography>,
  ];*/

  return (
    <Stack spacing={2}>
      <Breadcrumbs
        separator={<NavigateNextIcon fontSize="small" />}
        aria-label="breadcrumb"
      >
        {breadcrumbs}
      </Breadcrumbs>
    </Stack>
  );
}



function DashboardContainer(props:any) {
  return (
    <div>
        <Grid item xs={12} md={12} lg={12}  sx={{m:1}}>
            <CustomSeparator breadcrumbs={props.breadcrumbs}/>
        </Grid>
        <Grid item xs={12} md={12} lg={12}  sx={{m:1}}>
            {props.children}
        </Grid>
    </div>
  );
}

export default DashboardContainer;
