import { createSlice } from '@reduxjs/toolkit';

import axios from "axios";

const initialState:any = {
    first_name: "",
    last_name: "",
    name: "",
    email: "",
    gender: "",
    primary_contact: "",
    secondary_contact: "",
    address: ""
};

const leadSlice = createSlice({
    name: 'leads',
    initialState: initialState,
    reducers: {
        SET_FIRST_NAME: (state, action) => {
            return {
                ...state,
                first_name: action.payload.first_name,
            };
        },
        SET_LAST_NAME: (state, action) => {
            return {
                ...state,
                last_name: action.payload.last_name,
            };
        },
        SET_NAME: (state, action) => {
            return {
                ...state,
                name: action.payload.name,
            };
        },
        SET_EMAIL: (state, action) => {
            return {
                ...state,
                email: action.payload.email,
            };
        },
        SET_GENDER: (state, action) => {
            return {
                ...state,
                gender: action.payload.gender,
            };
        },
        SET_PRIMARY_CONTACT: (state, action) => {
            return {
                ...state,
                prmary_contact: action.payload.prmary_contact,
            };
        },
        SET_SECONDARY_CONTACT: (state, action) => {
            return {
                ...state,
                secondary_contact: action.payload.secondary_contact,
            };
        },
        SET_ADDRESS: (state, action) => {
            return {
                ...state,
                address: action.payload.address,
            };
        },
    }
});

export const { 
    SET_FIRST_NAME,
    SET_LAST_NAME,
    SET_NAME,
    SET_EMAIL,
    SET_GENDER,
    SET_PRIMARY_CONTACT,
    SET_SECONDARY_CONTACT,
    SET_ADDRESS,
} = leadSlice.actions

export default leadSlice.reducer